import { Component, OnInit } from '@angular/core';
import { PostProvider } from '../../providers/post-provider';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-add-sport-center',
  templateUrl: './add-sport-center.page.html',
  styleUrls: ['./add-sport-center.page.scss'],
})
export class AddSportCenterPage implements OnInit {

  sports: any = [];
  limit: number = 13; // LIMIT GET PERDATA
	start: number = 0;

  constructor(
    private router: Router,
    private postPvdr: PostProvider,
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
		this.sports = [];
		this.start = 0;
		this.loadSport();
  }
  
  loadData(event: any) {
		this.start += this.limit;
		setTimeout(() => {
			this.loadSport().then(() => {
				event.target.complete();
			});
		}, 500);
  }
  
  loadSport() {
		return new Promise(resolve => {
			let body = {
				aksi: 'getAllSport',
			};

			this.postPvdr.postData(body, 'process-api.php').subscribe(data => {
				for (let sport of data.result) {
					this.sports.push(
            sport
            );
				}
				resolve(true);
			});
		});
  }

  id: number;

  name: string ="";
  location: string ="";
  number: string="";
  link: string ="";
  img: string="";
  

  test()
  {
        name : this.name;
        location : this.location;
        number: this.number;
        link: this.link;
        img: this.img;
        

    console.log(this.name);
    console.log(this.location);
    console.log(this.link);
    console.log(this.number);
    console.log(this.link);
    console.log(this.img);
  }

  addSportCenter(){
  	return new Promise(resolve => {
  		let body = {
  			aksi : 'addSportCenter',
  			name : this.name,
        location : this.location,
        number: this.number,
        link: this.link,
        img: this.img
  		};

  		this.postPvdr.postData(body, 'process-api.php').subscribe(data => {
  			this.router.navigate(['/home']);
        console.log('OK');
        console.log(this.name);
    console.log(this.location);
    console.log(this.link);
    console.log(this.number);
    console.log(this.link);
    console.log(this.img);
  		});
  	});

  }

}
